
import { defineComponent, ref, PropType, onMounted } from 'vue';

export default defineComponent({
  name: 'LanguageInput',
  emits: ['update:value'],
  props: {
    value: {
      type: String,
      required: true,
    },
    label: {
      type: String,
      required: true,
    },
  },
  setup(props, { emit }) {
    const inputValue = ref<string>();
    const languages = ref([
      { value: 'en', label: 'English' },
      { value: 'id', label: 'Bahasa Indonesia' },
      { value: 'ca', label: 'Catal' },
      { value: 'da', label: 'Dansk' },
      { value: 'de', label: 'Deutsch' },
      { value: 'es', label: 'Espaol' },
      { value: 'fr', label: 'Franais' },
      { value: 'it', label: 'Italiano' },
      { value: 'hu', label: 'Magyar' },
      { value: 'nl', label: 'Nederlands' },
      { value: 'no', label: 'Norsk' },
      { value: 'pl', label: 'Polski' },
      { value: 'pt', label: 'Portugus' },
      { value: 'ro', label: 'Romn' },
      { value: 'sk', label: 'Slovenina' },
      { value: 'fi', label: 'Suomi' },
      { value: 'sv', label: 'Svenska' },
      { value: 'tl', label: 'Tagalog' },
      { value: 'vi', label: 'Ting Vit' },
      { value: 'tr', label: 'Trke' },
      { value: 'cs', label: 'etina' },
      { value: 'el', label: '' },
      { value: 'bg', label: '' },
      { value: 'ru', label: '' },
      { value: 'uk', label: '' },
      { value: 'ar', label: '' },
      { value: 'ms', label: ' ' },
      { value: 'hi', label: ' ' },
      { value: 'th', label: '' },
      { value: 'zh', label: '' },
      { value: 'ja', label: '' },
      { value: 'zh-hk', label: '' },
      { value: 'ko', label: '' },
      { value: 'asl', label: 'American Sign Language' },
      { value: 'other', label: 'Other' },
    ]);
    function onChange(event: any) {
      updateModel(event.value);
    }

    function updateModel(val: any) {
      emit('update:value', val);
    }

    return {
      onChange,
      inputValue,
      languages,
    };
  },
});
